*{
  margin: 0;
  padding: 0;
}
.app{
  height: 100vh;
  overflow-x: hidden;
  display: flex;
  background-color: #020230;
  color: white;
  margin: 0%;
  padding: 0%;
  
 
}
.main {
  width: 75%;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #020230), url("./imges/KBC-Set.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
}

.pyramid{
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.top{
  height: 50%;
  position: relative;
}

.timer{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 5px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: 700;
  position: absolute;
  bottom: 10px;
  top: 365px;
  left: 100px;
}

.bottom{
  height: 50px;
}

.trivia{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 191px;
}

.question{
  width: 80%;
  background: linear-gradient(#100241,black);
  background-color: black;
  border: 2px solid white;
  padding: 20px;
  text-align: center;
  font-size: 20px;
  border-radius: 10px;
}

.answer:hover,
.answer.active{
  background: mediumblue;
}

.answer.correct{
  animation: correct 3s ease forwards;
}

@keyframes correct{
  0%, 22%,42% {
    background: mediumblue;
  }
  20%, 40%,60%{
    background: linear-gradient(#0e0124,#22074d);
  }
  60%, 100%{
    background: green;
  }
}

.answer.wrong{
  animation: wrong 3s ease forwards;
}

@keyframes wrong{
  0%, 22%,42% {
    background: mediumblue;
  }
  20%, 40%,60%{
    background: linear-gradient(#0e0124,#22074d);
  }
  60%, 100%{
    background: crimson;
  }
}
.answers{
  width: 80%;
  margin-top: 23px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.answer{
  width: 40%;
  padding: 10px;
  text-align: center;
  background: linear-gradient(#0e0124,#22074d);
  border: 1px solid white;
  border-radius: 15px;
  font-size: 20px;
  font-weight: 300;
  cursor: pointer;
  margin: 0 10px 20px 30px;

}

.moneylistItem{
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
}
.moneylist{
  list-style: none;
  width: 100%;
  padding: 20px;
}

.moneyListItemNumber{
  font-size: 18px;
  font-weight: 100;
  width: 30%;
}

.moneylistItem.active{
  background-color: teal;
}

moneyListItemAmount{
  font-size: 20px;
  font-weight: 300;
}
.endText{
  position: relative;
  left: 0;
  right: 0; 
  top: 0;
  bottom: 0;
  margin: auto
}